
import { PDFDocument, StandardFonts, rgb } from 'pdf-lib'
import { getStore, updStore, ObjectKeys, parseDateMMDDYYYY } from '../../../stores/Utils';


function blobfromArray(pdfBytes) {
  let blob = new Blob([pdfBytes], { type: "application/pdf" });
  let urlCreator = window.URL || window.webkitURL;
  return urlCreator.createObjectURL(blob);
}



export async function generateTranslateBirthCertification(data, blob, add) {


  let element = getStore("print_structure")?.birth_certificate_struct_print || [];

  
  ObjectKeys(data).map(fld=>{
    if(element[fld] && data[fld]){
      element[fld].text = data[fld];
    }
  })

  
  const url2 = "https://www.hrmfinance.com/data/birth_certicate_tr.pdf";

  const existingPdfBytes = await fetch(url2).then((res) =>
    res.arrayBuffer()
  );

  

  const pdfDoc = await PDFDocument.load(existingPdfBytes,{ignoreEncryption: true})
  const helveticaFont = await pdfDoc.embedFont(StandardFonts.Helvetica);


  const pngUrl = getStore("b64_signature");
  let pngImage = null;
  // ||'https://www.hrmfinance.com/data/signature.png';
  if(pngUrl){
    const pngImageBytes = await fetch(pngUrl).then((res) => res.arrayBuffer())
    pngImage = await pdfDoc.embedPng(pngImageBytes);
  }



  const pages = pdfDoc.getPages()
  const firstPage = pages[0]
  const { width, height } = firstPage.getSize()
  updStore("pdfSize", { width, height, pages : pages.length })
  



 
 /*
  Array.from(Array(200).keys()).map(g=>{
    pages[0]?.drawText((g+1)*5%10? (g+1)*5 + "" :"|", {
      x: (g)*5,
      y: height - 280,
      size: 4,
      font: helveticaFont,      
      color:  rgb(0, 0.1, 0.1)
    });
    pages[0]?.drawText( height - (g+1)*5 + "- - - - - - - - -  - - - - - - - -  - - - - - - - - -", {
      x: 5,
      y: (g)*5,
      size: 4,
      font: helveticaFont,      
      color:  rgb(0, 0.1, 0.1)
    })
  })



 */





  add && element && ObjectKeys(element).map(elmId=>{
    let elm = element[elmId];
    if(elm){
      
      let pageNo = elm.pageNo;

      let pgSz = pages?.[pageNo]?.getSize();


     
      if(elm.type==="text"){
        let alph = 4
        pages[pageNo]?.drawText(elm.text?elm.text.toString() : "", {
          x: elm.x,
          y:  pgSz.height - elm.y ,
          size: elm.size || 10,
          font: helveticaFont,      
          color:elm.color ? rgb(elm.color[0]/255 , elm.color[1]/255 , elm.color[2]/255 ) :  rgb(0, 0.1, 0.1)
        })
      }
      else if(elm.type==="textboxn"){

        let letters = (elm.text?elm.text.toString() : elm.id.toString()).split('');
       
        letters?.map((lt, indx)=>{
          pages[pageNo]?.drawText(lt, {
            x: elm.x + (indx * elm.space),
            y:  pgSz.height - elm.y ,
            size: elm.size || 10,
            font: helveticaFont,      
            color:elm.color ? rgb(elm.color[0]/255 , elm.color[1]/255 , elm.color[2]/255 ) :  rgb(0, 0.1, 0.1)
          })
        })

        
      }
      else if(elm.type==="checkBox"){   
        let alph = 4     
        let yAx = pgSz?.height  - elm.y ;
        let svgPath = "M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z";
        pages[pageNo]?.moveTo( elm.x+2,yAx);
        pages[pageNo]?.drawSvgPath(svgPath, { borderColor: elm.color ? rgb(elm.color[0]/255 , elm.color[1]/255 , elm.color[2]/255 ) :  rgb(0, 0, 0), borderWidth: 1 , scale: 0.5})
        
      }
      else if(elm.type==="image"){
        pngImage && pages[pageNo].drawImage(pngImage, {
          x: elm.x,
          y:  pgSz.height - elm.y ,
          width:  elm.w,
          height: elm.h,
        })
      }
    }
  })

  /*
  firstPage.drawText('This text was added with JavaScript!', {
    x: 5,
    y: height / 2 + 300,
    size: 50,
    font: helveticaFont,
    color: rgb(0.95, 0.1, 0.1),
    rotate: degrees(-45),
  })

*/


pdfDoc.setTitle("Birth Certificate")
pdfDoc.setAuthor('HRM finance')
//pdfDoc.setSubject('ðŸ“˜ An Epic Tale of Woe ðŸ“–')
//pdfDoc.setKeywords(['eggs', 'wall', 'fall', 'king', 'horses', 'men'])
//pdfDoc.setProducer('PDF App 9000 ðŸ¤–')
pdfDoc.setCreator('')
pdfDoc.setCreationDate(new Date())
pdfDoc.setModificationDate(new Date())

if(blob){
 const pdfBytes = await pdfDoc.save();
  return blobfromArray(pdfBytes)
}
else{
  return pdfDoc.saveAsBase64({ dataUri: true });
}
 
  
 
}





